body {
  background-color: gray;
  color:white;
}

.game {
  display:flex;
  flex-direction:column;
  align-items:center;
}
.board-row{
  display:flex;
  flex-flow: row nowrap;
}
.square {
  background-color:gray;
  color:white;
  border: 1px solid black;
  padding:0;
  font-size:100px;
  text-align:center;
  width:100px;
  height:100px;
}
.square:focus{
  outline:none;
  background-color:black;
}